<template>
  <div>
    <ProfileBanner />
    <div class="container">
      <div class="row">
        <div
          class="col col-xl-9 order-xl-2 col-lg-9 order-lg-2 col-md-12 order-md-1 col-sm-12 col-12"
        >
          <div class="ui-block" ref="updateInfo">
            <div class="ui-block-title">
              <h6 class="title">Personal Information</h6>
            </div>
            <div class="ui-block-content">
              <!-- Personal Information Form  -->
              <response-message :data="errorData" />
              <form id="info_change_form" method="post" v-on:submit.prevent="updateInfo()">
                <div class="row">
                  <div class="col col-lg-6 col-md-6 col-sm-12 col-12">
                    <div :class=" userDetails.firstname !== null ? 'form-group label-floating' : 'form-group label-floating is-empty' ">
                      <label class="control-label"> {{ getUserDetails.user_type === '1' ? 'First Name' : 'Business Name' }} </label>
                      <input v-if="getUserDetails.user_type === '1' " class="form-control" v-model="formModel.firstname" id="firstname" placeholder type="text" value="John" />
                      <input v-else-if="getUserDetails.user_type === '2' " class="form-control" v-model="formModel.business_name" id="business_name" placeholder type="text" value="John Business" />
                    </div>

                    <div :class=" userDetails.email !== null ? 'form-group label-floating' : 'form-group label-floating is-empty' ">
                      <label class="control-label">Your Email</label>
                      <input
                        class="form-control"
                        placeholder
                        type="email"
                        v-model="formModel.email"
                        id="email"
                        value="support@matchdaydeals.com"
                      />
                    </div>
                    <div :class=" userDetails.club !== null && getUserDetails.user_type === '1' || formModel.business_address !== null ? 'form-group label-floating' : 'form-group label-floating is-empty' ">
                      <label class="control-label">{{ getUserDetails.user_type === '1' ? 'Fan Club Name' : 'Business Address' }}</label>
                      <input v-if="getUserDetails.user_type === '1'" class="form-control" v-model="formModel.club" id="club" placeholder type="text" />
                      <textarea
                              v-else
                              v-model="formModel.business_address"
                              name="review"
                              class="form-control"
                              placeholder
                              id="business_address"
                      ></textarea>
                    </div>
                  </div>

                  <div class="col col-lg-6 col-md-6 col-sm-12 col-12">

                    <div :class=" userDetails.lastname !== null && this.formModel.business_city !== null ? 'form-group label-floating' : 'form-group label-floating is-empty' ">
                      <label class="control-label">{{ getUserDetails.user_type === '1' ? 'Last Name' : 'Business City' }}</label>
                      <input v-if="getUserDetails.user_type === '1' " class="form-control" v-model="formModel.lastname" id="lastname" placeholder type="text" value="Doe" />
                      <input v-else class="form-control" v-model="formModel.business_city" id="business_city" placeholder type="text" value="Doe" />
                    </div>

                    <div :class=" userDetails.username !== null ? 'form-group label-floating' : 'form-group label-floating is-empty' ">
                      <label class="control-label">Your Username</label>
                      <input
                              class="form-control"
                              placeholder
                              type="url"
                              v-model="formModel.username"
                              id="username"
                              value="johndoe"
                              disabled
                      />
                    </div>

                    <div v-if="getUserDetails.user_type === '2' " :class="this.formModel.business_country !== null ? 'form-group label-floating' : 'form-group label-floating is-empty' ">
                      <label class="control-label">Business Country</label>
                      <input class="form-control" v-model="formModel.business_country" id="business_country" placeholder type="text" />
                    </div>

                    <div v-if="getUserDetails.user_type !== '1'" :class="getUserDetails.user_type === '2' ? 'form-group label-floating' : 'form-group label-floating is-empty' ">
                      <label class="control-label">Business Description</label>
                      <textarea
                              v-model="formModel.business_description"
                              name="review"
                              class="form-control"
                              placeholder
                              id="business_description"
                      ></textarea>
                    </div>

                  </div>

                  <div class="col col-lg-6 col-md-6 col-sm-12 col-12">
                    <button type="submit" class="btn bg-mdd btn-lg full-width" >Update Profile Info</button>
                  </div>
                </div>
              </form>

              <!-- ... end Personal Information Form  -->
            </div>
          </div>
        </div>

        <settingsBar :data="SettingsData" />
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import ProfileBanner from "../../components/Profile/ProfileBanner";
import settingsBar from "@/components/Profile/SettingsRightBar";
import Footer from "../../components/Home/Footer";
import {mapState} from "vuex";
import jquery from "jquery";

// let $ = jquery;

var pageData = {
  projectName: "",
  userData: {},
  id: null,
  avatar: "",
  signedIn: false,
  SettingsData: {
      active: 'Account',
  },
  formModel: {
    email: '',
    firstname: '',
    lastname: '',
    website: '',
    DOB: '',
    phone: '',
    gender: '',
    facebook: '',
    twitter: '',
    club: '',
    username: '',
    business_name: '',
    business_city: '',
    business_address: '',
    business_country: '',
    business_description: '',
  },
  errorData: {},
  userDetails: {},
};

export default {
  name: "AccountInfo",
  data() {
    return pageData;
  },
  components: { settingsBar, Footer, ProfileBanner },
  computed: mapState({
    getAvatar: state => state.user.avatar,
    getUsername: state=> state.user.username,
    getUserDetails: state => state.user,
  }),
  methods: {
    showLoading() {
			let loader = this.$loader(this, this.$refs.updateInfo, {loaderType: "spinner"});
			return loader;
		},
    // validate form fields
    validateForm() {
      if (this.getUserDetails.user_type === '1') {
        if (
                this.formModel.email === "" &&
                this.formModel.firstname === "" &&
                this.formModel.lastname === "" &&
                this.formModel.club === "" &&
                this.formModel.username === ""
        ) {
          $("#info_change_form .label-floating").addClass('has-error');
          $("#info_change_form input").addClass('form-control-danger');
          this.errorData = {
            message: "Sorry all fields are required",
            isError: true
          };
          return false;
        }else if (
                !this.$validate("nametext", this.formModel.firstname)
        ) {
          $("#info_change_form input#firstname").parent(".label-floating").addClass('has-error')
          $("#info_change_form input#firstname").addClass('form-control-danger')
          this.errorData = {
            message: "Sorry your Firstname Characters are wrong",
            isError: true
          }
          return false;
        }else if (
                !this.$validate("nametext", this.formModel.lastname)
        ) {
          $("#info_change_form #lastname").parent(".label-floating").addClass('has-error')
          $("#info_change_form input#lastname").addClass('form-control-danger')
          this.errorData = {
            message: "Sorry your Lastname Characters are wrong",
            isError: true
          }
          return false;
        }else if (
                !this.$validate("email", this.formModel.email)
        ) {
          $("#info_change_form #email").parent(".label-floating").addClass('has-error')
          $("#info_change_form input#email").addClass('form-control-danger')
          this.errorData = {
            message: "Sorry your Email Characters are wrong",
            isError: true
          }
          return false;
        }

        else if (
                !this.$validate("longtext", this.formModel.username)
        ) {
          $("#info_change_form #username").parent(".label-floating").addClass('has-error')
          $("#info_change_form input#username").addClass('form-control-danger')
          this.errorData = {
            message: "Sorry your Username Characters are wrong",
            isError: true
          }
          return false;
        }

        else {
          $("#info_change_form .label-floating").removeClass('has-error');
          $("#info_change_form input").removeClass('form-control-danger');
          return true;
        }
      }else {
        if (
                this.formModel.business_name === "" || this.formModel.business_name === null &&
                this.formModel.business_address === "" || this.formModel.business_address === null &&
                this.formModel.business_country === "" || this.formModel.business_country === null &&
                this.formModel.business_city === "" || this.formModel.business_city === null &&
                this.formModel.business_description === "" || this.formModel.business_description === null &&
                this.formModel.username === "" || this.formModel.username === null
        ) {
          $("#info_change_form .label-floating").addClass('has-error');
          $("#info_change_form input").addClass('form-control-danger');
          this.errorData = {
            message: "Sorry all fields are required",
            isError: true
          };
          return false;
        }else if (
                !this.$validate("email", this.formModel.email)
        ) {
          $("#info_change_form #email").parent(".label-floating").addClass('has-error')
          $("#info_change_form input#email").addClass('form-control-danger')
          this.errorData = {
            message: "Sorry your Email Characters are wrong",
            isError: true
          }
          return false;
        }

        else if (
                !this.$validate("longtext", this.formModel.username)
        ) {
          $("#info_change_form #username").parent(".label-floating").addClass('has-error')
          $("#info_change_form input#username").addClass('form-control-danger')
          this.errorData = {
            message: "Sorry your Username Characters are wrong",
            isError: true
          }
          return false;
        }

        else {
          $("#info_change_form .label-floating").removeClass('has-error');
          $("#info_change_form input").removeClass('form-control-danger');
          return true;
        }
      }
    },
    // submit the password change form
    updateInfo() {
      // validate the form first
      if(this.validateForm()) {
        let loader = this.showLoading();
        const requestPayload = {
          firstname: this.formModel.firstname,
          lastname: this.formModel.lastname,
          email: this.formModel.email,
          club: this.formModel.club,
        };

        const requestPayloadBusiness = {
          business_name: this.formModel.business_name,
          address: this.formModel.business_address,
          city: this.formModel.business_city,
          country: this.formModel.business_country,
          business_desc: this.formModel.business_description,
        };

        if (this.getUserDetails.user_type === '1') {
          this.axios.post('/profile/update', requestPayload)
        .then(response => {
            loader.hide();
            this.errorData = {
              message: response.data.message,
              isError: "success"
            };
            this.fetchUserDetails();
          })
                  .catch(error => {
                    // console.log(error)
                    loader.hide();
                    this.errorData = {
                      message: error.response
                              ? "Please check your Profile Details"
                              : "Sorry an error occured, check your internet",
                      isError: true
                    };
                  })
        }else {
          this.axios.post('/business/update', requestPayloadBusiness)
        .then(response => {
            loader.hide();
            this.errorData = {
              message: response.data.message,
              isError: "success"
            };
            this.fetchUserDetails();
          })
                  .catch(error => {
                    // console.log(error)
                    loader.hide();
                    this.errorData = {
                      message: error.response
                              ? "Please check your Profile Details"
                              : "Sorry an error occured, check your internet",
                      isError: true
                    };
                  })
        }

      }
    },
    // fetch the users details
    fetchUserDetails() {
      var loader = this.showLoading();
      this.axios.get('/user/details')
      .then(response => {
        loader.hide();
        // console.log(response.data.data)
        this.userDetails = response.data.data;
        this.formModel.email = this.userDetails.email;
        this.formModel.firstname = this.userDetails.firstname;
        this.formModel.lastname = this.userDetails.lastname;
        this.formModel.username = this.userDetails.username;
        this.formModel.club = this.userDetails.club;

        if (this.getUserDetails.user_type === '2') {
          this.formModel.business_address = this.userDetails.address;
          this.formModel.business_description = this.userDetails.business_desc;
          this.formModel.business_name = this.userDetails.business_name;
          this.formModel.business_city = this.userDetails.city;
          this.formModel.business_country = this.userDetails.country;
        }
        window.eventBus.$emit("loadUserData", true);
      })
      .catch(error => {
        this.errorData = {
          message: error.response
            ? "An error from our end"
            : "Sorry an error occured, check your internet",
          isError: true
        };
        loader.hide();
      })
    },
    checkUserData(userData) {
      if (userData !== "" || userData !== "undefined") {
        this.userData = JSON.parse(userData);
        this.signedIn = true;
      }
    },
  },
  mounted() {
    var userData = localStorage.fansData;
    // load the user details
    this.fetchUserDetails();
    if (userData) {
      this.checkUserData(userData);
    }

    $("title").html("Account Settings | " + this.$store.state.projectName);

    CRUMINA.Bootstrap = function() {
      //  Activate the Tooltips
      $('[data-toggle="tooltip"], [rel="tooltip"]').tooltip();

      // And Popovers
      $('[data-toggle="popover"]').popover();

      /* -----------------------------
       * Replace select tags with bootstrap dropdowns
       * Documentation about used plugin:
       * https://silviomoreto.github.io/bootstrap-select/
       * ---------------------------*/
      $(".selectpicker").selectpicker();

      /* -----------------------------
       * Date time picker input field
       * Script file: daterangepicker.min.js, moment.min.js
       * Documentation about used plugin:
       * https://v4-alpha.getbootstrap.com/getting-started/introduction/
       * ---------------------------*/
      var date_select_field = $('input[name="datetimepicker"]');
      /*moment.locale('uk');*/
      if (date_select_field.length) {
        var start = moment().subtract(29, "days");

        date_select_field.daterangepicker({
          minDate: "12/05/1900",
          startDate: start,
          autoUpdateInput: false,
          singleDatePicker: true,
          showDropdowns: true,
          locale: {
            format: "DD/MM/YYYY"
          }
        });
        date_select_field.on("focus", function() {
          $(this)
            .closest(".form-group")
            .addClass("is-focused");
        });
        date_select_field.on("apply.daterangepicker", function(ev, picker) {
          $(this).val(picker.startDate.format("DD/MM/YYYY"));
          $(this)
            .closest(".form-group")
            .addClass("is-focused");
        });
        date_select_field.on("hide.daterangepicker", function() {
          if ("" === $(this).val()) {
            $(this)
              .closest(".form-group")
              .removeClass("is-focused");
          }
        });
      }
    };

    $(document).ready(function() {
      CRUMINA.Bootstrap();
    });
  }
};
</script>

<style>
</style>
